.Wrapper {
  max-width: 80%;
  margin: auto;
  text-align: center;
}

.rowWrapper {
  display: flex;
}

.start-btn {
  width: 100px;
  height: inherit;
  border-color: black;
  background-color: blue;
}

/* CSS */
.button {
  padding: 0.6em 2em;
  border: none;
  outline: none;
  color: rgb(255, 255, 255);
  background: #111;
  cursor: pointer;
  position: relative;
  z-index: 0;
  border-radius: 10px;
  user-select: none;
  -webkit-user-select: none;
  touch-action: manipulation;
  margin: 30px;
}

.disabled {
  padding: 0.6em 2em;
  border: none;
  outline: none;
  color: rgb(60, 63, 88);
  background: #111;
  cursor: pointer;
  position: relative;
  z-index: 0;
  border-radius: 10px;
  user-select: none;
  -webkit-user-select: none;
  touch-action: manipulation;
  margin: 30px;
}

.button:hover {
  background-color: rgb(30, 30, 30);
}
